@import "./../../vars";

.footer {
  background-color: $bg-color-black;
  padding: 0 50px;
  margin: 0;
  text-align: center;

  &__links {
    margin: auto 0;
    padding-top: 20px;
    &-a {
      margin: 0 10px;
      color: $t-color-white;
    }

    &-a:hover {
      color: $t-color-red;
    }

    &-icon {
      width: 30px;
      height: 30px;
    }
  }

  &__connection {
    display: grid;
    justify-content: center;
    grid-template-columns: 280px 160px;
    grid-column-gap: 30px;

    &-link {
      color: $t-color-white;
    }

    &-link:hover {
      color: $t-color-red;
    }

    &-text {
      color: $t-color-white;
    }
  }

  &__copytext {
    color: $t-color-white;
    margin: 0;
    padding-bottom: 16px;
  }
}

@media screen and (max-width: 540px) {
  .footer {
    &__links {
      padding: 32px 0 16px 0;
    }

    &__connection {
      grid-template-columns: 1fr;
      grid-column-gap: 0;
    }

    &__copytext {
      padding-top: 16px;
      padding-bottom: 32px;
    }
  }
}
