@import "./../../vars";

.navbar {
  background-color: $bg-color-black;
  margin: 0;

  &__container {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: center;
    padding: 15px 50px;
  }

  &__logo-w{
    height: 30px;
  }

  &__toggle {
    display: none;
  }

  &__menu {
    padding: 0;
    display: flex;
    align-items: center;
    gap: 30px;
  }

  &__menu-link {
    color: $t-color-white;
    font-size: 20px;
    transition: 0.3s ease;
    text-transform: capitalize;

    &.active,
    &:hover {
      color: $t-color-red;
    }
  }
}

@media screen and (max-width: 768px) {
  .navbar {
    &__container {
      padding: 15px 20px;
    }

    &__toggle {
      display: block;
    }

    &__menu {
      display: none;

      &--show {
        padding-top: 25px;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 15px;
        width: 100%;
      }
    }
  }
}

@media (min-width: 767.9px) and (max-width: 1000px) {
  .navbar {
    &__container {
      padding: 15px 30px;
    }
  }
}
