@import "./../../vars";
.popout {
    display: none;
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.6);
    top: 0;
    left: 0;
    overflow-y: auto;
    overflow-x: hidden;
    z-index: 3;

    &__container {
        min-height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__body {
        width: 80%;
        min-height: 495px;
        display: flex;
        position: relative;
        background-color: #fff;
        color: #000;
        position: relative;
    }

    &__close-icon {
        position: absolute;
        top: 30px;
        right: 35px;
    }

    &__overlay {
        position: absolute;
        top: 0;
        left: 0;
        content: "";
        width: 50%;
        height: 100%;
        background-image: url("./../../images/popout-bg.jpg");
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
    }

    &__overlay2 {
        width: 50%;
    }

    &__main {
        margin: 100px 35px 0 35px;
    }

    &__title {
        font-size: 30px;
        margin: 0 0 10px 0;
    }

    &__text {
        margin: 0;
        margin-bottom: 50px;
    }

    &__links {
        display: flex;
        justify-content: space-between;

        &-a {
            color: $t-color-black;
        }

        &-a:hover {
            color: $t-color-red;
        }

        &-icon {
            width: 35px;
            height: 35px;
        }
    }
}

.popout.active {
    display: block !important;
}

@media (min-width: 769px) and (max-width: 1000.98px) {
    .popout {
        &__overlay {
            width: 40%;
        }

        &__overlay2 {
            width: 40%;
        }

        &__main {
            margin: 100px 20px 0 20px;
        }

        &__close-icon {
            right: 20px;
        }
    }
}

@media screen and (max-width: 768.98px) {
    .popout {
        &__overlay {
            display: none;
        }

        &__overlay2 {
            display: none;
        }

        &__main {
            margin: 100px 35px 0 35px;
        }

        &__close-icon {
            right: 35px;
        }
    }
}
