@import "./../../vars";

.home {
  background-image: url("./../../images/mp-bg.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  color: $t-color-white;
  padding: 40px 0;

  &__container {
    display: flex;
    flex-wrap: wrap;
    padding: 0 50px;
    align-items: center;
  }

  &__left {
    padding-top: 120px;
    padding-bottom: 120px;

    &-title {
      font-size: 32px;
      margin: 0 0 5px 0;
    }

    &-subtitle {
      margin: 0 0 20px 0;
      font-size: 24px;
      font-weight: 600px;
    }

    &-links {
      margin: 0 0 25px;
      padding: 0;
      list-style: none;
      gap: 20px;
      display: flex;
    }

    &-text {
      font-size: 18px;
      width: 50%;
      margin-bottom: 30px;
    }

    &-cta {
      display: inline-block;
      background-color: lighten($color: $bg-color-red, $amount: 10);
      padding: 10px 42px;
      transition: 0.3 ease;
      border-radius: 5px;
      color: white;
      &:hover {
        background-color: $bg-color-red;
        transition: 0.2s ease;
        transform: scale(1.1);
      }
    }
  }
}

@media (max-width: 1000px) {
  .home {
    &__left {
      max-width: 100%;

      &-text {
        width: auto;
      }
    }
  }
}

@media (max-width: 563.98px) {
  .home {
    &__left {
      &-subtitle {
        height: 2rem;
        margin-bottom: 40px;
      }
    }
  }
}

@media (max-width: 353.98px) {
  .home {
    &__left {
      &-subtitle {
        height: 2rem;
        margin-bottom: 60px;
      }
    }
  }
}

.ma {
  &__title {
    font-size: 30px;
    text-align: center;
    margin: 70px 0 50px 0;
  }

  &__main {
    background-color: rgb(97, 114, 230);

    &-btn {
      width: 50%;
      border: 3px solid $br-color-grey;
      border-left: 2px solid $br-color-grey;
      border-right: 1px solid $br-color-grey;
      background-color: $bg-color-grey2;
      color: white;
      padding: 10px 0;
      font-size: 18px;
    }

    &-container {
      padding: 0 50px;
      height: 500px;
    }
  }

  &__carousel {
    background-color: $bg-color-grey1;
    border-radius: 10px;
    width: 600px;
    height: 340px;
    margin: 80px 0 0 0;
    overflow: hidden;
  }

  &__carousel-items {
    display: flex;
    transition: 0.5s;
  }

  &__carousel-item {
    width: 340px;
    margin: 14px 0;
  }

  &__carousel-item:nth-child(1) {
    margin-left: 125px;
  }

  &__carousel-prev-btn {
    position: absolute;
    background: none;
    margin: 144px 0 0 10px;
    border: none;
    z-index: 2;
  }

  &__carousel-next-btn {
    position: absolute;
    margin: 144px 0 0 530px;
    background: none;
    border: none;
    z-index: 2;
  }
}
