@import "./../../vars";

.about {
  text-align: center;
  &__title{
    font-size: 30px;
    margin-bottom: 0;
  }

  &__title2{
    font-size: 25px;
  }

  &__skills {
    margin: 50px 0;

    &-links {
      margin: 0;
      padding: 0;
      display: flex;
      justify-content: space-around;
      align-items: center;
      gap: 20px;
    }

    &-item {
      background: $bg-color-black;
      width: 20%;
      border-radius: 5px;
      list-style: none;
      color: $t-color-white;
      align-items: center;
      padding: 10px;
      gap: 5px;
    }

    &-icon{
      margin-right: 5px;
    }
  }

  &__use{
    margin-bottom: 50px;
  }
}

@media screen and (max-width :768px) {
  .about{
    text-align: center;
  &__skills{
    &-links {
      flex-wrap: wrap;
    }
    &-item {
      width: 30%;
    }
  }
}
}