@import "./../../../vars";

.posts {
    padding: 0;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 20px;
    grid-row-gap: 20px;

    &__item {
        border-radius: 3px;
        border: 1px solid $bg-color-black;
        padding: 20px 20px 40px 20px;
        list-style: none;
        min-width: 45%;
    }

    &__main {
        display: flex;
        justify-content: space-between;
        margin-bottom: 20px;
    }

    &__category {
        margin: auto 0;
    }

    &__title {
        margin: 10px 0;
    }

    &__link {
        border-radius: 3px;
        border: none;
        background: $bg-color-black;
        color: $t-color-white;
        padding: 10px 30px;
    }
    &__link:hover {
        background: $bg-color-red;
        padding: 11px 32px;
        transform: scale(1.1);
    }
}

@media screen and (max-width: 768px) {
    .posts {
        grid-template-columns: 1fr;
    }
}
