@import "./../../../vars";

.pagination{
    display: flex;
    padding: 0;
    justify-content: space-between;
    margin-top: 30px;
    margin-bottom: 30px;
    
    &__item{
        list-style: none;
        border: 1px solid $bg-color-black;
        border-radius: 3px;
        padding: 5px 10px;
        text-decoration: none;
    }

    &__item-a{
        text-decoration: none;
        font-family: 18px;
        color: $t-color-black;
    }

    &__item-a:hover{
        color: $t-color-red;
    }
}

nav{
    text-align: center;
}